
.modal-mask {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
}
.modal-mask .modal {
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 1rem;
  color: #fff;
}
.home-bg {
  width: 100%;
  height: 100%;
  background: url('~@/assets/body-bg.jpg');
  background-size: 100% 100%;
}
.home-bg .modal {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.home-bg .modal .modal-close {
  position: absolute;
  right: 0;
  top: 0;
  width: 2.1rem;
  height: 2.1rem;
  border-radius: 50%;
  background-image: url('~@/assets/close.png');
  background-size: 100% auto;
  background-repeat: no-repeat;
}



